*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: DM Sans, sans-serif;
  font-style: normal;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

p, h1, h2, h3 {
  margin: 0;
}

button {
  border: 0;
  padding: 0;
}

input {
  margin: 0;
  padding: 0;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  min-width: 320px;
  max-width: 767px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
