@media screen and (max-width: 1199.98px) {
  .header-content {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    z-index: 90;
    width: 100%;
    height: 100vh;
    background-color: #e17992;
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 60px 0;
    transition: transform .7s cubic-bezier(.175, .885, .32, 1.275), opacity .6s cubic-bezier(.175, .885, .32, 1.275);
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    overflow-x: scroll;
    transform: translateX(100%);
  }
}

